.container {
    display: flex;
    flex-wrap: wrap;
    margin: 7px;
    justify-content: center;
    align-items: center;
    gap: 0.1rem;
    list-style-type: none;
  }
  .crumb {
    text-align: center;
    user-select: none;
  
    font-size: 14px;
    width: 32px;
    display: block;
    padding: 0.5rem 0.3rem;
    text-decoration: none;
    border-radius: 0.2rem;
    box-shadow: 6px 3px, 8px, 3px rgba(0, 0, 0, 0.7);
    color: white;
    background-color: orange;
  }
  .crumb_active {
    text-align: center;
    user-select: none;
    margin: 20px 5px;
    font-size: 14px;
    width: 32px;
    display: block;
    padding: 0.5rem 0.3rem;
    text-decoration: none;
    border-radius: 0.2rem;
    border: 2px solid #d7d7d7;
    background-color: rgb(253, 255, 253);
    color: orange;
    border-color: orange;
    transform: scale(1.3, 1.3);
  }